import React from 'react'
import Link from 'gatsby-link'

import SideNavGD from '../../../components/sidenav-gd'
import homeimage from '../../../images/home-page-image.jpg'

const Gerd = () => (
  <div className="service-content-resources">
    <SideNavGD />
    <div className="right-side">
      <img alt="Home Page Image" src={homeimage} className="img-responsive" />
      <h1 className="main-subject">GastroEsophageal Reflux Disease (GERD)</h1>		
      <p>
        Almost everyone has experienced heartburn. About 25 million American adults 
        suffer daily from heartburn. It is a burning sensation felt behind the 
        breastbone and sometimes in the neck and throat. Heartburn is caused by
        stomach acid refluxing or splashing up into the esophagus-the muscular 
        tube that connects the throat to the stomach. 
        <br/>
        <br/>
        Occasional heartburn is nothing to be concerned about. However, anyone 
        who has heartburn on a regular basis should consult a physician. Constant
        exposure to stomach acid can irritate the lining of the esophagus and 
        cause other medical problems. Serious heartburn is known as gastroesophageal
        reflux disease or GERD.
      </p>
      <h2 className="sub-topic">What is GERD ?</h2>
      <p>
        The esophagus carries food and liquid into the stomach. At the lower end 
        of the esophagus where it enters the stomach, there is a strong muscular
        ring called the lower esophageal sphincter (LES). 
        The LES should remain tightly closed, except to allow food and liquid
        to pass into the stomach. Reflux occurs when the LES is not working 
        properly. It may relax for periods of time throughout the day and night,
        or it may be constantly too weak to function effectively. This allows 
        the stomach's acid juices to flow into the esophagus. How severe the
        disease becomes depends on how weakened the LES is, and the amount and
        duration of acid refluxed into the esophagus.
        <br/>
        <br/>
        It is also common to find a hiatal hernia complicating GERD. With a 
        hiatal hernia, the upper part of the stomach actually pushes up 
        into the chest through a weakness in the diaphragm. The diaphragm
        is the thin, flat muscle that separates the lungs from the abdomen. 
        <br/>
        <br/>
        When part of the upper stomach is stuck above the diaphragm, stomach
        acid is retained there for a longer period and is more likely 
        to reflux into the esophagus.
      </p>
      <h2 className="sub-topic">Symptoms and Causes of GERD</h2>
      <p>
        Frequent heartburn is the most common symptom. However, patients may also
        experience some of the following: sour bitter taste, bitter stomach 
        fluid coming into the mouth, especially during sleep, hoarseness, 
        repeatedly feeling a need to clear the throat, difficulty swallowing
        food or liquid, wheezing or coughing at night , worsening of symptoms
        after eating, or when bending over or lying down.
        <br/>
        <br/>
        Certain diet and lifestyle choices can contribute to the condition.
        For example, certain foods increase acid production, and fatty foods 
        increase the time food remains in the stomach. 
        <br/>
        <br/>	
        Chocolate, peppermint, coffee, alcoholic beverages, and especially
        nicotine in cigarette smoke weakens or relax the LES. Obesity places
        added pressure on the upper abdomen and during pregnancy the LES 
        weakens for unknown reasons.
      </p>
      <h2 className="sub-topic">Complications</h2>
      <p>
        Complications occur when GERD is severe or long-standing. Constant
        irritation of the esophagus by stomach acid can lead to inflammation,
        ulcers, and bleeding. Anemia or low blood count may develop. Over time,
        scarring and narrowing of the esophagus can also develop, making it 
        difficult to swallow foods and liquids. This narrowing is called a 
        stricture. Some patients develop a condition called Barrett's esophagus,
        which is a serious change in the cells lining the esophagus. 
        Barrett's esophagus may be a forerunner of esophageal cancer.
      </p>
      <h2 className="sub-topic">Diagnosis of GERD</h2>
      <p>
        The physician can usually suspect the diagnosis, simply by taking the medical history.
				Beyond that, the physician may order an x-ray examination of the esophagus and 
				stomach. For this x-ray, the patient swallows a liquid containing barium. 
				This makes it possible to see the reflux and a hiatal hernia on the x-ray.
				<br/>
        <br/>
				Endoscopy is the most important test for patients with GERD. It is done with the 
				patient under sedation. The physician examines the lining of the esophagus and 
				stomach with a thin, lighted, flexible tube called an endoscope.
				This exam is helpful in determining how severe the disease is, 
				how much tissue damage there is, and if there are any complications.
				Certain conditions, such as narrowing or stricture in the esophagus,
				can usually be corrected during this procedure. During an endoscopy,
				the physician can also look for signs of Barrett's esophagus, 
				and perform a biopsy to see if precancerous changes have occurred.
				<br/>
        <br/>
				There are other tests that are helpful. A pressure recording of 
				the esophagus is called esophageal manometry. It measures the 
				pressure in the LES and any abnormal muscle contractions in the
				main part of the esophagus. Finally, a 12 to 24 hour recording 
				of the acidity in the esophagus is often helpful.
      </p>
      <h2 className="sub-topic">What is the Treatment ?</h2>
      <p>
        Treatment is aimed at reducing reflux and damage to the lining of 
        the esophagus. Initial treatment is with lifestyle changes. 
        Patients with GERD should follow these recommendations:
      </p>
      <ul>
        <li>
          Avoid eating anything within three hours before bedtime. 
        </li>
        <li>
          Stop smoking. Nicotine in the blood weakens the LES. 
        </li>
        <li>
          Avoid fatty foods, milk, chocolate, spearmint, peppermint, 
          caffeine, citrus fruits and juices, tomato products, pepper 
          seasoning, and alcohol-especially red wine. 
        </li>
        <li>
          Decrease portions of food at mealtime, and avoid tight 
          clothing or bending over after eating. 
        </li>
        <li>
          Review all medications with the physician. Certain drugs 
          can weaken the LES, allowing acid irritation of the esophagus. 
        </li>
        <li>
          Elevate the head of the bed or mattress 6 to 8 inches. 
          This helps to keep acid in the stomach by gravity when 
          sleeping. Extra pillows by themselves are not very helpful. 
        </li>
        <li>
          Lose weight if overweight. This may relieve upward 
          pressure on the stomach and LES.
        </li>
      </ul>
      <p>
        Lifestyle changes are often all that is needed to correct mild forms of GERD.
        When symptoms are bad or GERD is moderate to severe, the physician will 
        prescribe medications. Some that reduce stomach acid include Tagamet, Zantac,
        Pepcid and Axid. Certain potent newer drugs are most frequently used when GERD
        is severe. Other medications, such as Propulsid and Reglan tighten the LES.
        These may be especially useful at night when reflux often occurs. A number
        of patients with GERD may need surgery to strengthen the LES. This procedure
        is called fundoplication. Previously this surgery required a major operation,
        often through the chest. 
        <br/>
        <br/>	
        It is a difficult operation for the patient, with a long recovery time.
        It is now usually done by laparoscopy. This is a new type of minimally
        invasive surgery, performed with a tiny incision at the naval and a 
        few needlepoints in the upper abdomen. The patient usually returns 
        home 1-2 days after surgery, with few problems. Surgery, however, 
        should not be considered until all other measures have been tried. 
        Surgery is often seriously considered for an otherwise healthy 
        patient when the disease is severe, or the patient does not want 
        to face the expense or regimen of long-term treatment with medications.
      </p>
      <h2 className="sub-topic">Summary</h2>
      <p>
        GERD is a common problem that requires medical attention when symptoms and
				tissue damage become troublesome. Fortunately, there is a great deal that 
				can now be done about GERD. Lifestyle changes can help, and there are 
				many medications and even surgery to treat and correct GERD. Working 
				together with the physician, a good medical program can almost always
				be developed to successfully treat the patient with GERD.
      </p>
    </div>
  </div>
)

export default Gerd



